.greetingSteps {
  text-align: center;
  margin: 2rem;
  h1 {
    font-size: 1.5rem;
    margin-bottom: 2rem;
  }
}
.fourSteps {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  padding: 4rem 0;
  background-color: $color-light-gray;
  .pipe{
    margin: 1rem auto;
    background-color: $color-black;
    width: 3.5rem;
    height: 1px;
  }
  img {
    margin-bottom: 1rem;
  }
}
.greetingButton {
  text-align: center;
  margin: 4rem 0;
  a {
    @include linkButton;
    margin-bottom: 1rem;
  }
}


